import PropTypes from 'prop-types';
import { styled } from 'styled-components';

import CopyButton from './CopyButton';

/**
 * Wrapper for the component that is visible when resting
 */
const Resting = styled.span``;

/**
 * Wrapper for the component that is visible when the user hovers over the
 * component
 */
const Action = styled(CopyButton)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

/**
 * Wrapper that controls the transition between the resting/hover states
 */
const Wrapper = styled.div`
  position: relative;

  ${Resting}, ${Action} {
    transition: opacity 0.3s ease-in-out;
  }

  ${Resting} {
    opacity: ${({ $isCopied }) => (!$isCopied ? 1 : 0)};
  }

  ${Action} {
    opacity: ${({ $isCopied }) => ($isCopied ? 1 : 0)};
  }

  &:hover {
    ${Resting} {
      opacity: 0;
    }

    ${Action} {
      opacity: 1;
    }
  }
`;

const Copyable = ({ className, children, value }) => {
  return (
    <Wrapper className={className}>
      <Resting>{children}</Resting>
      <Action isCosy={false} value={value} />
    </Wrapper>
  );
};

Copyable.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  value: PropTypes.string.isRequired,
  timeout: PropTypes.number,
};

export default Copyable;
